@import url("https://fonts.googleapis.com/css2?family=Monoton&family=Quicksand:wght@500;700&display=swap");
button {
  background-color: #ffebb3;
  color: #5a3e2b;
  font-family: inherit;
  border: none;
  border-radius: 10rem;
  cursor: pointer;
  text-transform: uppercase;
}

select {
  background-color: #ffebb3;
  color: #5a3e2b;
  font-family: inherit;
  border: none;
  border-radius: 10rem;
  cursor: pointer;
}

input {
  background-color: #ffebb3;
  color: #5a3e2b;
  font-family: inherit;
  border: none;
  border-radius: 10rem;
  cursor: pointer;
}

.add-form button {
  background-color: #76c7ad;
}
.add-form button,
.add-form select,
.add-form input {
  padding: 1.2rem 3.2rem;
  font-weight: 700;
  font-size: 1.8rem;
}

.list button,
.list select {
  padding: 0.8rem 2.4rem;
  font-weight: 700;
  font-size: 1.4rem;
  text-transform: uppercase;
  margin: 0 0.8rem;
}
.list input[type=checkbox] {
  width: 2rem;
  height: 2rem;
  accent-color: #f4a226;
}
.list li button {
  cursor: pointer;
  background: none;
  border: none;
  font-size: 1.8rem;
  padding: 0.8rem;
  transform: translateY(2px);
}

@media (max-width: 768px) {
  .add-form button, .add-form select, .add-form input {
    padding: 0.8rem 2.6rem;
    font-weight: 700;
    font-size: 1.4rem;
  }
  .list .actions button, .list .actions select {
    padding: 0.6rem 1.6rem;
    font-weight: 700;
    font-size: 1.2rem;
  }
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}
html body {
  font-size: 2.4rem;
  font-family: sans-serif;
  color: #5a3e2b;
  font-family: "Quicksand";
  font-weight: 500;
}
html body .app {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-rows: auto auto 1fr auto;
}

@media (max-width: 768px) {
  body {
    font-size: 2rem;
  }
}
@media (max-width: 360px) {
  body {
    font-size: 1.8rem;
  }
}
h1 {
  text-align: center;
  background-color: #f4a226;
  font-family: "Monoton";
  font-size: 8rem;
  text-transform: uppercase;
  font-weight: 400;
  word-spacing: 30px;
  letter-spacing: -5px;
  padding: 2.4rem 0;
}

@media (max-width: 768px) {
  h1 {
    padding: 1rem 0;
    font-weight: 400;
    font-size: 6rem;
  }
}
@media (max-width: 360px) {
  h1 {
    padding: 0 0;
    font-weight: 400;
    font-size: 4rem;
    word-spacing: 10px;
  }
}
.add-form {
  background-color: #e5771f;
  padding: 2.8rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.8rem;
}
.add-form h3 {
  margin-right: 1.6rem;
  font-size: 2.4rem;
}

@media (max-width: 768px) {
  .add-form {
    width: 100%;
    padding: 1rem 0;
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
  }
  .add-form * {
    margin: 1rem;
  }
}
@media (max-width: 360px) {
  .add-form {
    padding: 1rem 0;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    gap: 0;
    padding: 0;
  }
  .add-form h3 {
    font-size: 2rem;
  }
}
.list {
  background-color: #5a3e2b;
  color: #ffebb3;
  padding: 4rem 0;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 3.2rem;
  align-items: center;
}
.list ul {
  list-style: none;
  width: 80%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.2rem;
  justify-content: center;
  align-content: start;
}
.list ul li {
  display: flex;
  align-items: center;
  gap: 1.2rem;
}

@media (max-width: 360px) {
  .list ul {
    width: 80%;
    gap: 2rem;
  }
  .list ul li {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
  }
  .list .actions {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }
}
.stats {
  background-color: #76c7ad;
  text-align: center;
  font-weight: 700;
  padding: 3.2rem 0;
}

@media (max-width: 768px) {
  .stats {
    padding: 2rem 0;
  }
}
@media (max-width: 360px) {
  .stats {
    padding: 1rem 0;
  }
}