@import 'variables';
@import 'mixins';

button {
	@include buttons-inputs;
	text-transform: uppercase;
}
select {
	@include buttons-inputs;
	}
input {
	@include buttons-inputs;
}

.add-form {
	button {
		background-color: $footer-color;
	}
	button,
	select,
	input {
		@include padding-size(1.2rem, 3.2rem, 700, 1.8rem);
	}
}

.list {
	button,
	select {
		@include padding-size(0.8rem, 2.4rem, 700, 1.4rem);
		text-transform: uppercase;
		margin: 0 0.8rem;
	}
	input[type='checkbox'] {
		@include mix-size(2rem, 2rem);
		accent-color: $logo-color;
	}
	li button {
		cursor: pointer;
		background: none;
		border: none;
		font-size: 1.8rem;
		padding: 0.8rem;
		transform: translateY(2px);
	}
}

@media (max-width: 768px) {
	.add-form{
		button, select, input {
			@include padding-size(0.8rem, 2.6rem, 700, 1.4rem);	
		}
	}
	.list {
		.actions {
			button, select {
				 @include padding-size(0.6rem, 1.6rem, 700, 1.2rem);
			 }
		}
	}
}