@import 'variables';
@import 'mixins';

h1 {
	text-align: center;
	background-color: $logo-color;
	font-family: 'Monoton';
	font-size: 8rem;
	text-transform: uppercase;
	font-weight: 400;
	word-spacing: 30px;
	letter-spacing: -5px;
	padding: 2.4rem 0;
}

@media (max-width: 768px){
	h1 {
		@include padding-size(1rem, 0, 400, 6rem);
	}
}

@media (max-width: 360px){
	h1 {
		@include padding-size(0, 0, 400, 4rem);
		word-spacing: 10px;
	}
}