@import 'variables';

.list {
	background-color: $main-color;
	color: $input-color;
	padding: 4rem 0;
	display: flex;
	justify-content: space-between;
	flex-direction: column;
	gap: 3.2rem;
	align-items: center;
	ul {
		list-style: none;
		width: 80%;
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
		gap: 1.2rem;
		justify-content: center;
		align-content: start;
		li {
			display: flex;
			align-items: center;
			gap: 1.2rem;
		}
	}
}

@media (max-width: 360px) {
	.list {
		ul {
			width: 80%;
			gap: 2rem;
			li {
				display: flex;
				align-items: center;
				flex-direction: row;
				justify-content: space-between;
			}
		}
		.actions {
			display: flex;
			flex-direction: column;
			align-items: center;
			gap: 1rem;
		}
	}
}
