@import 'variables';

@mixin mix-size($fx, $fy) {
	width: $fx;
	height: $fy;
}

@mixin buttons-inputs {
  background-color: $input-color;
	color: $main-color;
	font-family: inherit;
	border: none;
	border-radius: 10rem;
	cursor: pointer;
}

@mixin padding-size($fptb, $fplr, $fw, $fs) {
  padding: $fptb $fplr;
  font-weight: $fw;
  font-size: $fs;
}