@import 'mixins';

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  body {
    font-size: 2.4rem;
    font-family: sans-serif;
    color: #5a3e2b;
    font-family: "Quicksand";
    font-weight: 500;
    .app {
      @include mix-size(100%, 100vh);
      display: grid;
      grid-template-rows: auto auto 1fr auto;
    }
  }
}

@media (max-width: 768px) {
	body{
		font-size: 2rem;
	}
}

@media (max-width: 360px) {
	body{
		font-size: 1.8rem;
	}
}