@import 'variables';

.add-form {
	background-color: $form-color;
	padding: 2.8rem 0;
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 0.8rem;
	h3 {
		margin-right: 1.6rem;
		font-size: 2.4rem;
	}
}

@media (max-width: 768px) {
	.add-form {
    width: 100%;
    padding: 1rem 0;
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
		* {
      margin: 1rem;
		}
	}
}

@media (max-width: 360px) {
	.add-form {
        padding: 1rem 0;
        display: flex;
        flex-direction: column;
        align-items: stretch;
        gap: 0;
        padding: 0;
    h3 {
      font-size: 2rem;
    }
	}
}
